import React from "react";
import { Link } from "react-router-dom";
import "./UserInfo.css";

function UserInfo({ user }) {
  return (
    <div id="my_profile" className="my_profile">
      <div className="my_wrap">
        <div className="my_cont">
          <div className="my_info">
            <div className="info_area">
              <a href="/api/auth/logout" id="btn_logout" className="btn_logout">
                LOGOUT
              </a>
              <a href="#myinfo" className="user_thumb">
                <img
                  id="MY_PROFILE"
                  src="Deafult-Profile-Pitcher.png"
                  alt="default"
                  width="56"
                  height="56"
                />
              </a>
              <div className="user_info">
                <div className="info_box">
                  <span className="nickname MY_NICKNAME">{user.nickname}</span>
                  <Link to={{
                    pathname:"/myinfo",
                    state: {email: user.email, nickname: user.nickname}
                  }} className="link_my">내정보</Link>
                  <div className="email MY_EMAIL">{user.email}</div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default UserInfo;
