import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEye, faShoppingBasket } from "@fortawesome/free-solid-svg-icons";

function MovieItem({ movieTitle, state, pickType, clickMovieList }) {
  return (
    <li className={`movie_item ${state}`} onClick={clickMovieList}>
      <span className="mitem">{movieTitle}</span>
      {pickType === "b"? <FontAwesomeIcon icon={faShoppingBasket} size="xs" className="pick_type"/> : <FontAwesomeIcon icon={faEye} size="xs" className="pick_type"/> }
      
    </li>
  );
}

export default MovieItem;
