import React from "react";
import "./Movie.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAngleRight,
  faStar,
  faTrashAlt
} from "@fortawesome/free-solid-svg-icons";
import MovieItem from "./MovieItem";
import $ from "jquery";
import axios from "axios";

class Movie extends React.Component {
  constructor(props) {
    super(props);
    this.props = props;
  }

  state = {
    selectedMovieIdx: 0
  };

  clickMovieList = (e) => {
    $.each($(".movie_item"), (idx, item) => { item.className = "movie_item movie_item_inactive"})
    e.currentTarget.className = "movie_item movie_item_active"
    this.setState({selectedMovieIdx: $(e.currentTarget).index()});
  }

  clickMovieDelete = () => {
    const {selectedMovieIdx} = this.state;
    const movieId = this.props.movies[selectedMovieIdx].id;

    axios.delete(`/api/movie/${movieId}`).then(() => window.location.reload()).catch(err => console.error(err));
  }

  render() {
    const {isAuth, movies} = this.props;
    const {selectedMovieIdx} = this.state;
    let SELECTED_MOVIE;
    if(movies !== undefined) SELECTED_MOVIE = movies[selectedMovieIdx];

    return (
      <div className="movie_box">
        <div className="movie_list_area">
          <ul className="movie_list">
            {isAuth
              ? movies.map((movie, index) =>
                  index === 0 ? (
                    <MovieItem
                      key={index}
                      movieTitle={movie.title}
                      pickType={movie.basket? "b" : "w"}
                      clickMovieList={this.clickMovieList}
                      state="movie_item_active"
                    />
                  ) : (
                    <MovieItem
                      key={index}
                      movieTitle={movie.title}
                      pickType={movie.basket? "b" : "w"}
                      clickMovieList={this.clickMovieList}
                      state="movie_item_inactive"
                    />
                  )
                )
              : null}
          </ul>
        </div>
  
        {movies === undefined || movies.length === 0 ? null :
          (
            <div className="selected_movie">
              <div className="movie_hash_tag">{ SELECTED_MOVIE.genres.map(g => `#${g.genre} `) }</div>
              <div className="about_movie">
                <div className="movie_poster">
                  <img
                    src={ SELECTED_MOVIE.posterUrl }
                    alt={ SELECTED_MOVIE.title }
                  />
                  <h5>{ SELECTED_MOVIE.subtitle }</h5>
                </div>
                <div className="movie_info">
                  <div className="movie_info_1">
                    <span className="country">{ SELECTED_MOVIE.countries.length === 0? "-" : SELECTED_MOVIE.countries.map((c, idx) => idx === 0? c.country : `, ${c.country}`) }</span>
                    <span className="running_time">{ SELECTED_MOVIE.runningTime? SELECTED_MOVIE.runningTime: "-" }</span>
                    <span className="public_date">{ SELECTED_MOVIE.pubDate? SELECTED_MOVIE.pubDate: "-" }</span>
                  </div>

                  <div className="movie_info_2">
                    <span className="movie_director">{ SELECTED_MOVIE.directors.length === 0? "-" : SELECTED_MOVIE.directors[0].name }</span>
                    <span className="movie_GPA">
                      <FontAwesomeIcon icon={faStar} color="red" />
                      &nbsp;{ SELECTED_MOVIE.userRating }
                    </span>
                  </div>
                  
                  <p className="movie_info_3">
                      <strong className="story_title">{ SELECTED_MOVIE.story.split("||")[0] }</strong>
                      { SELECTED_MOVIE.story.split("||")[0].length === 0? null : <br></br>}
                      <span className="story_content">{ SELECTED_MOVIE.story.split("||")[1] }</span>
                  </p>
                </div>
              </div>
              <div className="delete_movie">
                <FontAwesomeIcon icon={faTrashAlt} size="sm" onClick={this.clickMovieDelete}/>
              </div>
              <div className="movie_info_detail">
                <a href={ SELECTED_MOVIE.link } className="to_detail" rel="noreferrer" target="_blank">
                  자세히&nbsp;
                  <FontAwesomeIcon icon={faAngleRight} />
                </a>
              </div>
            </div>
          )
        }
      </div>
    );
  }
}

export default Movie;
