import React from "react";
import Navigation from "../components/Navigation";
import Footer from "../components/Footer";
import { Link } from "react-router-dom";
import axios from "axios";
import $ from "jquery";
import "./MyInfo.css";

class MyInfo extends React.Component {
    componentDidMount() {
        const { location} = this.props;
        if(location.state === undefined) window.location.href = "/";
    }

    clickUpdateBtn = () => {
        const updateParam = $("#user_update_form").serialize();
        axios.put("/api/user", updateParam).then(() => window.location.href = "/api/auth/logout").catch(err => console.error(err));
    }

    clickDropbtn = () => {
        axios.delete("/api/user").then(() => window.location.href = "/api/auth/logout").catch(err => console.error(err));
    }

    render() {
        if(this.props.location.state) {
            const {email, nickname} = this.props.location.state;
            return (
                <div id="wrap">
                    <Navigation />
                    <div id="container">
                        <div className="info_section">
                            <div className="info">
                                <div className="info_header">
                                    <h3>My Information</h3>
                                </div>
                                <div className="info_content">
                                    <form id="user_update_form">
                                        <p>email : {email}</p>
                                        <p>
                                            <label>nickname : <input type="text" name="nickname" className="info_item" defaultValue={nickname}/></label>
                                        </p>
                                        <p>
                                            <label>password : <input type="password" name="password" className="info_item"/></label>
                                        </p>
                                        <p>
                                            <label>new password : <input type="password" name="newPassword" className="info_item"/></label>
                                        </p>
                                        <p>
                                            <label>new password(confirm) : <input type="password" name="newPasswordConfirm" className="info_item"/></label>
                                        </p>
                                    </form>
                                </div>
                                <div className="info_footer">
                                    <Link to="/" className="info_btn">Home</Link>
                                    <span className="info_btn drop_out_btn" onClick={this.clickDropbtn}>Drop</span>
                                    <span className="info_btn update_info_btn" onClick={this.clickUpdateBtn}>Update</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <Footer />
                </div> 
            )
        } else return null;
    }
}

export default MyInfo;