import React from "react";
import "./RegistRepoModal.css";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle } from "@fortawesome/free-solid-svg-icons";
import { faGithub } from "@fortawesome/free-brands-svg-icons"

function RegistRepoModal() {

    const clickClose = () => {
        document.getElementById("regist_repo_modal").classList.add("md_hidden");
        document.getElementById("name").value = "";
        document.getElementById("username").value = "";
    }

    const registRepo = () => {
        const name = document.getElementById("name").value;
        const username = document.getElementById("username").value;
        const url = `https://github.com/${username}/${name}`;
        const repo = {name, url};
        axios.post("/api/repo", repo).then(() => {
            window.location.reload();
        }).catch(err => console.error(err));
    }

    return(
        <div id="regist_repo_modal" className="regist_repo_modal md_hidden">
            <div className="md_wrap">
                <div className="md_header">
                    <h4><FontAwesomeIcon icon={faGithub} size="lg" />&nbsp;&nbsp;Regist Repository</h4>
                    </div>
                <div className="md_content">
                    <input autoComplete="off" type="text" id="username" name="username" className="github_username" placeholder="Github username"/>
                    <input autoComplete="off" type="text" id="name" name="name" className="repo_name" placeholder="Repository name"/>
                </div>
                <div className="md_footer">
                    <FontAwesomeIcon icon={faCircle} size="xs" className="md_close_btn md_btn" onClick={clickClose}/>
                    <FontAwesomeIcon icon={faCircle} size="xs" className="md_regist_btn md_btn" onClick={registRepo}/>
                </div>
            </div>
        </div>
    )
}

export default RegistRepoModal;