import React from "react";
import "./Container.css";
import Weather from "./column_left/Weather";
import Movie from "./column_left/Movie";
import ProfileBox from "./column_right/ProfileBox";
import Repository from "./column_right/Repository";
import OP_GG from "./column_right/OP_GG";

function Container({ isAuth, user }) {
  return (
    <div id="container">
      <div className="column_left">
        <Weather />
        <Movie isAuth={isAuth} movies={user.movies} />
      </div>
      <div className="column_right">
        <div className="column_fix_wrap">
          <ProfileBox isAuth={isAuth} user={user} />
          <Repository isAuth={isAuth} repos={user.repos} />
          <OP_GG />
        </div>
      </div>
    </div>
  );
}

export default Container;
