import React from "react";
import { getEngChampName } from "../../data/lol_champions"
import "./OP_GG.css";

function OP_GG() {
    let selectedOPGGSearchType;
    function clickOPGGSearchType(e) {
        if(selectedOPGGSearchType === undefined) selectedOPGGSearchType = document.getElementById("OPGG_record");
        if(e.target === selectedOPGGSearchType) return false;
        else {
            selectedOPGGSearchType.className = "type_tab";
            selectedOPGGSearchType = e.target;
            selectedOPGGSearchType.className += " type_tab_active";
        }
    }
    
    function searchOPGG(e) {
        if(selectedOPGGSearchType === undefined) selectedOPGGSearchType = document.getElementById("OPGG_record");
        function funcExe() {
            const check_kor = /[ㄱ-ㅎ|ㅏ-ㅣ|가-힣]/;
            let search = document.getElementById("op_query").value;
            if(selectedOPGGSearchType.dataset.ref === "champion/" && check_kor.test(search)) search = getEngChampName(search);
            const toHref = "https://www.op.gg/" + selectedOPGGSearchType.dataset.ref + search;
            document.getElementById("op_query").value = "";
            window.open(toHref,"_blank");
        }

        if(e.keyCode === 13) funcExe();
        if(e.type ==="click") funcExe();
    }

    return (
        <div className="op_gg">
            <div className="OP_GG"><span>OP.GG</span><img src="./lol_logo_png.png" alt="리그오브레전드"/></div>
            <div className="search_types">
                <ul>
                    <li id="OPGG_record" className="type_tab type_tab_active" data-ref="summoner/userName=" onClick={clickOPGGSearchType}>전적</li>
                    <li id="OPGG_champion" className="type_tab" data-ref="champion/" onClick={clickOPGGSearchType}>챔피언</li>
                </ul>
            </div>
            <div className="search_box">
                <input type="text" name="op_query" id="op_query" className="input_search" onKeyDown={searchOPGG}/>
                <button className="op_search_btn" onClick={searchOPGG}>.GG</button>
            </div>
        </div>
    )
}
export default OP_GG;