import React from "react";
import Navigation from "../components/Navigation";
import Footer from "../components/Footer";
import axios from "axios";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch, faStar, faSpinner } from "@fortawesome/free-solid-svg-icons";
import "./Movie.css";
import $ from "jquery";
import MovieItem from "../components/MovieRoute/MovieItem";

class Movie extends React.Component {
    state = {
        searchMovieList : [],
        selectedMovie: -1,
        searched: "",
        isMovieDetailLoading : true,
        movieDetailInfo: {}
    }

    searchMovie = () => {
        const query = $(".search_input").val();
        axios.get("/api/movie/search", {params:{query}}).then(response => {
            if(response.data.movies.length === 0) this.setState({searchMovieList:undefined})
            else this.setState({searchMovieList: response.data.movies, searched: " searched", selectedMovie: -1, isMovieDetailLoading: true});
        });
    }

    enterSearchMovie = (e) => {
        if(e.keyCode === 13) this.searchMovie();
    }

    registerMovie = (e) => {
        const selectedMovieIdx = this.state.selectedMovie;
        const selectedMovie = this.state.searchMovieList[selectedMovieIdx]
        const selectedMovieDetail = this.state.movieDetailInfo;
        const movie = {
            title: selectedMovie.title,
            subtitle: selectedMovie.subtitle,
            link: selectedMovie.link,
            pubYear: selectedMovie.pubYear,
            directors: selectedMovie.directors,
            actors: selectedMovie.actors,
            userRating: selectedMovie.userRating,
            story: selectedMovieDetail.story,
            posterUrl: selectedMovieDetail.posterUrl,
            thumnailUrl: selectedMovieDetail.thumnailUrl,
            genres: selectedMovieDetail.genres,
            countries: selectedMovieDetail.countries,
            runningTime: selectedMovieDetail.runningTime,
            pubDate: selectedMovieDetail.pubDate,
            basket: false,
            watched: false,
        }
        
        if(e.target.className === "basket_btn") movie.basket = true;
        else movie.watched = true;

        axios.post("/api/movie/regist", movie).then(response => console.log(response));
    }

    componentDidMount() {
        $(document).off().on("click", ".search_result_item",(e) => {
            if(e.currentTarget.className === "search_result_item") {
                this.setState({ isMovieDetailLoading : true });

                $.each($(".search_result_item"), (idx, item) => {
                    item.className = "search_result_item";
                });
    
                e.currentTarget.className = "search_result_item selected_item";

                const code = e.currentTarget.dataset.movieCode;
                axios.get(`/api/movie/search/${code}`).then(response => {
                    const { detail } = response.data;
                    this.setState({ selectedMovie : $(e.currentTarget).index(), movieDetailInfo : detail, isMovieDetailLoading : false })
                }).catch(err => console.log(err));
            }
        });
    }

    render() {
        const {searchMovieList, selectedMovie, searched, movieDetailInfo, isMovieDetailLoading} = this.state;

        return(
            <div id="wrap">
                <Navigation />
                <div id="container">
                    <div className="movie_search_box">
                        <div className="search_section">
                            <input className="search_input" type="text" onKeyDown={this.enterSearchMovie}></input>
                            <button className="movie_search_btn" onClick={this.searchMovie}><FontAwesomeIcon icon={faSearch} /></button>
                        </div>
                        {searchMovieList === undefined? <div className="search_result">검색 결과가 없습니다.</div> : (
                            <div className={`search_result${searched}`}>
                                <div className={`search_result_section${searched}`}>
                                    <ul className="search_result_list">
                                        {searchMovieList.map((item, idx) => (
                                            selectedMovie === idx?
                                            <MovieItem 
                                                selected={" selected_item"}
                                                imgUrl={item.image} 
                                                title={item.title}
                                                director={item.directors[0]}
                                                userRate={item.userRating}
                                                movieCode={item.link.split("=")[1]}
                                                key={idx}
                                            /> : 
                                            <MovieItem
                                                selected={""}
                                                imgUrl={item.image} 
                                                title={item.title}
                                                director={item.directors[0]}
                                                userRate={item.userRating}
                                                movieCode={item.link.split("=")[1]}
                                                key={idx}
                                            />
                                        ))}
                                    </ul>
                                </div>
                                {selectedMovie === -1? null :
                                    (
                                        isMovieDetailLoading === true? <div className="movie_detail_loading"><FontAwesomeIcon className="fa-spin" icon={faSpinner} size="lg"/></div> :
                                            (
                                                <div className="selected_movie_detail">
                                                    <div className="search_movie_title">
                                                        <span className="mv_title">{searchMovieList[selectedMovie].title}</span>
                                                        <strong>{searchMovieList[selectedMovie].subtitle}, {searchMovieList[selectedMovie].pubYear}</strong>
                                                    </div>
                                                    <div className="search_movie_content">
                                                        <div className="search_movie_left">
                                                            <div className="search_movie_poster">
                                                                <img className="movie_poster" src={movieDetailInfo.posterUrl} alt="interstella"/>
                                                            </div>
                                                        </div>
                                                        <div className="search_movie_right">
                                                            <dl className="search_movie_info">
                                                                <dt className="mv_spec"><strong>개요</strong></dt>
                                                                <dd>
                                                                    <span className="mv_genre after_divline">{movieDetailInfo.genres.length === 0? "-" : movieDetailInfo.genres.join(", ")}</span>
                                                                    <span className="mv_country after_divline">{movieDetailInfo.countries.length === 0? "-" : movieDetailInfo.countries.join(", ")}</span>
                                                                    <span className="mv_runningTime after_divline">{movieDetailInfo.runningTime.length === 0? "-" : movieDetailInfo.runningTime}</span>
                                                                    <span className="mv_pubDate">{movieDetailInfo.pubDate.length === 0? "-" : movieDetailInfo.pubDate}</span>
                                                                </dd>
                                                                <dt className="mv_spec"><strong>감독</strong></dt>
                                                                <dd className="mv_director">{searchMovieList[selectedMovie].directors.length === 0? "-" : searchMovieList[selectedMovie].directors.join(", ")}</dd>
                                                                <dt className="mv_spec"><strong>평점</strong></dt>
                                                                <dd className="mv_userRating"><FontAwesomeIcon icon={faStar} color="red" />&nbsp;{searchMovieList[selectedMovie].userRating}</dd>
                                                                <dt className="mv_spec"><strong>배우</strong></dt>
                                                                <dd className="mv_actor">{searchMovieList[selectedMovie].actors.length === 0? "-" : searchMovieList[selectedMovie].actors.join(", ")}</dd>
                                                                <dt className="mv_spec"><strong>줄거리</strong></dt>
                                                                <dd className="mv_story">
                                                                    <p className="mv_story_title">{movieDetailInfo.story.split("||")[0]}</p>
                                                                    <p className="mv_story_content">{movieDetailInfo.story.split("||")[1]}</p>
                                                                </dd>
                                                            </dl>
                                                        </div>
                                                    </div>
                                                    <div className="register_movie_btn">
                                                        <button className="basket_btn" onClick={this.registerMovie}>찜</button>
                                                        <button className="watched_btn" onClick={this.registerMovie}>본 영화</button>
                                                    </div>
                                                </div>
                                            )
                                    )
                                    
                                }
                            </div>
                        )}
                    </div>
                </div>
                <Footer />
            </div>
        )
    }
}

export default Movie;