import React from "react";
import "./Navigation.css"
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSearch } from "@fortawesome/free-solid-svg-icons";
import $ from "jquery";
import { Link } from "react-router-dom";

function Navigation() {
    $(document).ready(() => {
        let curPage;
        if (window.location.href === "http://localhost:3003/" || window.location.href === "http://localhost:3003/#") curPage = "home"
        else curPage = window.location.href.split("http://localhost:3003/")[1];
        $(".nav_item_select").removeClass("nav_item_select");
        $(`#${curPage}`).addClass("nav_item_select");
    });
    
    function search() {
        const SEARCH_GOOGLE = "https://www.google.com/search?q=";
        const SEARCH_NAVER = "https://search.naver.com/search.naver?query=";
        const SEARCH_ENGINE = document.getElementById("se_select").value;
        let toHref = "";

        if(SEARCH_ENGINE === "naver") toHref += SEARCH_NAVER;
        if(SEARCH_ENGINE === "google") toHref += SEARCH_GOOGLE;
        toHref += document.getElementById("query").value;
        document.getElementById("query").value = "";
        window.open(toHref,"_blank");
    }

    function enterSearch(e) {
        if(e.keyCode === 13) search();
    }

    function clickSearch() {
        search();
    }

    return (
        <div id="header" role="banner">
            <div className="header_bg">
                <div className="group_flex">
                    <div className="logo_area">
                        {/* <a href="/"><img src="pick.png" width="150px" alt="pick_logo" className="logo"/></a> */}
                    </div>
                    <div className="search_area">
                        <div className="search_space">
                            <select id="se_select" className="se_select">
                                <option value="google" defaultChecked>GOOGLE</option>
                                <option value="naver">NAVER</option>
                            </select>
                            <input autoComplete="off" className="input_text" type="text" name="query" id="query" onKeyDown={enterSearch}/>
                            <button id="btn_search_submit" className="btn_search_submit" onClick={clickSearch}><FontAwesomeIcon icon={faSearch} size="lg"/></button>
                        </div>
                    </div>
                    <div className="emoticon"><img src="snow.gif" alt="snow"/></div>
                </div>
            </div>
            <div id="gnb">
                <div className="gnb_inner">
                    <div className="group_nav">
                        <ul className="list_nav type_fix">
                            <li id="home" className="nav_item nav_item_select"><Link to="/" role="tab">Home</Link></li>
                            {/* <li id="apis" className="nav_item"><a href="/" role="tab">APIs</a></li> */}
                            <li id="sic" className="nav_item"><a href="/sic" role="tab">SIC</a></li>
                            <li id="movie" className="nav_item"><Link to="/movie">Movie</Link></li>
                            <li id="github" className="nav_item"><a href="https://github.com" rel="noreferrer" target="_blank" role="tab">Github</a></li>
                            <li id="opgg" className="nav_item"><a href="https://www.op.gg/" rel="noreferrer" target="_blank" role="tab">OP.GG</a></li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    )
}

export default Navigation;