import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faStar } from "@fortawesome/free-solid-svg-icons";
import "./MovieItem.css";

function MovieItem({selected, imgUrl, title, director, userRate, movieCode}) {
    return (
        <li className={`search_result_item${selected}`} data-movie-code={movieCode}>
                <img className="movie_item_poster" src={imgUrl} alt={`${title} Thumnail`}/>
                <div className="summary_title">
                    <p>{title}</p>
                </div>
                <div className="summary_director">
                    <p>{director}</p>
                </div>
                <div className="summary_userRate"><FontAwesomeIcon icon={faStar} color="red"/>&nbsp;<span>{userRate}</span></div>
        </li>
    )
}

export default MovieItem;