import React from "react";
import "./Login.css";
import SignupModal from "./SignupModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus } from "@fortawesome/free-solid-svg-icons";

function Login() {
  const clickSignup = () => {
    document.getElementById("signup_modal").classList.remove("md_hidden");
  };

  return (
    <div id="login_box" className="login_box">
      <div className="login_wrap">
        <form className="login_form" action="/api/auth/login" method="post">
          <input
            className="input_email"
            type="email"
            name="email"
            id="email"
            placeholder="email"
          />
          <input
            className="input_password"
            type="password"
            name="password"
            id="password"
            placeholder="password"
          />
          <button className="btn_login_local btn_login" type="submit">
            LOGIN
          </button>
          <a href="/api/auth/login/naver" className="btn_login_naver btn_login">
            NAVER
          </a>
          <a
            href="/api/auth/login/google"
            className="btn_login_google btn_login"
          >
            GOOGLE
          </a>
        </form>
        <button className="btn_find">FIND</button>
        <button className="btn_signup" onClick={clickSignup}>
          <FontAwesomeIcon icon={faPlus} size="sm" />
        </button>
      </div>
      <SignupModal />
    </div>
  );
}

export default Login;
