import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCircle } from "@fortawesome/free-solid-svg-icons";
import axios from "axios";

function RepoItem({ repo }) {
  const goToRepository = (repoUrl) => {
    const toHref = repoUrl;
    window.open(toHref, "_blank");
  }

  const clickRepoDelete = () => {
    axios.delete(`/api/repo/${repo.id}`).then(() => window.location.reload()).catch(err => console.error(err));
  }

  return (
    <li className="repo_item">
      <span className="repo_name" onClick={() => goToRepository(repo.url)}>{repo.name}</span>
      <span className="delete_repo">
        <FontAwesomeIcon icon={faCircle} size="xs" className="delete_repo_btn" onClick={clickRepoDelete}/>
      </span>
    </li>
  );
}

export default RepoItem;
