import React from "react";
import "./Repository.css";
import RepoItem from "./RepoItem";
import RegistRepoModal from "./RegistRepoModal";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRegistered } from "@fortawesome/free-solid-svg-icons";


function Repository({ isAuth, repos }) {
  const clickRegistBtn = () => {
    document.getElementById("regist_repo_modal").classList.remove("md_hidden");
  }


  return (
    <div className="github_repo">
      <div className="github">
        <span>Github Repository</span>
        {isAuth ? <FontAwesomeIcon icon={faRegistered} size="1x" className="repo_regist_btn" onClick={clickRegistBtn}/> : null}
      </div>
      <div className="repository">
        <ul className="list_repo">
          {isAuth
            ? repos.map((repo, index) => <RepoItem repo={repo} key={index} />)
            : null}
        </ul>
      </div>
      <RegistRepoModal />
    </div>
  );
}

export default Repository;
